<template>
  <div class="orderDetail mescroll">
    <div class="orderInfo">
      <div class="title">订单信息</div>
      <div class="flex">
        <div class="flex-1">订单编号</div>
        <div class="flex-2">{{detail.order_no}}</div>
      </div>
      <div class="flex">
        <div class="flex-1">订单状态</div>
        <div
          class="flex-2"
          :style="detail.order_status == 1 || detail.order_status == 3?'color:#eb5252':'color:#999'"
        >{{$utils.getText(detail.order_status)}}</div>
      </div>
      <div class="flex">
        <div class="flex-1">联系人：</div>
        <div class="flex-2">{{detail.receive_man}}</div>
      </div>
      <div class="flex">
        <div class="flex-1">手机号码</div>
        <div class="flex-2">{{detail.contact_phone}}</div>
      </div>
      <div class="flex">
        <div class="flex-1">核销码：</div>
        <div class="flex-2">{{detail.write_off_code}}</div>
      </div>
      <div
        class="flex"
        v-if="detail.vehicle_brand"
      >
        <div class="flex-1">车辆品牌：</div>
        <div class="flex-2">{{detail.vehicle_brand}}</div>
      </div>
      <div
        class="flex"
        v-if="detail.license_plate_number"
      >
        <div class="flex-1">车牌号：</div>
        <div class="flex-2">{{detail.license_plate_number}}</div>
      </div>
      <div class="flex">
        <div class="flex-1">下单时间</div>
        <div class="flex-2">{{detail.create_time}}</div>
      </div>
      <div
        class="flex"
        v-if="detail.order_status != 1 && detail.order_status != 11"
      >
        <div class="flex-1">支付方式</div>
        <div class="flex-2">{{detail.pay_type == 1?'余额':'微信'}}</div>
      </div>
      <div
        class="flex"
        v-if="detail.order_status != 1 && detail.order_status != 11"
      >
        <div class="flex-1">支付时间</div>
        <div class="flex-2">{{detail.pay_time}}</div>
      </div>
    </div>
  </div>
</template>
<script>
// import { generateActivityOrderDetail } from "@/api";
export default {
  data() {
    return {
      detail: {}
    };
  },
  methods: {
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      generateActivityOrderDetail({
        activity_order_id: this.$route.query.id
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.detail = res.data;
        }
      });
    }
  },
  mounted() {
    document.title = "订单详情";
    this.loadData();
  }
};
</script>
